*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Manrope";
}

.recboxBtnBox{
  display: flex;
}

.recboxBtn{
  text-align: center;
  width: 50%;
  background: white;
  outline: none;
  padding: 10px 72px;
  font-size: 16px;
  color: rgb(35, 137, 177);
  border: 1px solid rgb(35, 137, 177);
}

.recboxBtnmobile{
  text-align: center;
  width: 50%;
  background: white;
  outline: none;
  padding: 10px 35px;
  font-size: 16px;
  color: rgb(35, 137, 177);
  border: 1px solid rgb(35, 137, 177);
}


.consent_btn { 
  background-color: #ffb500;
  color: black;
  text-transform: capitalize;
  border-radius: 5px;
  padding: 15px 20px;
  font-size: 21px;
  width: 170px;
  outline: none;
  border: none;
  margin-top: 25px;
  cursor: pointer;
  text-decoration: none; 
  border: none; 
  box-shadow: 7px 6px 28px 1px rgba(0, 0, 0, 0.24); 
  cursor: pointer; 
  transition: 0.2s all; 
  font-weight: 500;
  display: flex;
  align-items: center;
  justify-content: center;
} 
/* Adding transformation when the button is active */ 

.consent_btn:active { 
  transform: scale(0.99); 
  /* Scaling button to 0.98 to its original size */ 
  box-shadow: 3px 2px 22px 1px rgba(0, 0, 0, 0.24); 
  /* Lowering the shadow */ 
} 

.consent_btn:hover{
  background-color: #F79F1F;
}

.consent_btn_disable { 
  background-color: gray;
  color: white;
  text-transform: capitalize;
  border-radius: 5px;
  padding: 15px 20px;
  font-size: 21px;
  width: 170px;
  outline: none;
  margin-top: 25px;
  cursor: pointer;
  text-decoration: none; 
  border: none; 
  cursor: pointer; 
  font-weight: 500;
  display: flex;
  align-items: center;
  justify-content: center;
} 

@keyframes scale-div {
  to {
    transform: perspective(100px) scale(1.2,1.2) translateZ(5px) 
  }

	/* 0%		{text-shadow:  0 0 100px #df0d0d;}
	5%		{text-shadow:  0 0 90px #df0d0d;} */
	0%		{opacity: 1;}
	10%		{opacity: 1;}
	15%		{opacity: 0.78;}
	/* 20%		{text-shadow:  0 0 0px #df0d0d;}
	80%		{text-shadow:  0 0 0px #df0d0d;} */
	85%		{opacity: 0.9 #bcdf0d;}
	/* 95%		{text-shadow:  0 0 90px #df0d0d;}
	100%	{text-shadow:  0 0 100px #df0d0d;}  */

}

#progressimg {
  animation: scale-div 0.5s infinite linear alternate;
}

.buttons1223{
  float: left; padding: 30px; width: 100%;
}

.buttons1223count{
  float:left;
}

html {
  --progress: 0%;
  --color:#FFB500;
}
